.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.button {
  border-radius: 15px;
  background: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
  outline: none;
  cursor: pointer;
  margin: 5px;
}

.App-header {
  background-color: var(--tg-theme-bg-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--tg-theme-text-color);
}

.App-link {
  color: #61dafb;
}

.Tabs-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.Tab-service{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);;
  border-width: 1px;
  margin: 10px;
  width: 300px;
  height: 50px;
}

.Tab-specialist{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);;
  border-width: 1px;
  margin: 10px;
  width: 300px;
  height: 50px;
}

.Tab-date{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);;
  border-width: 1px;
  margin: 10px;
  width: 300px;
  height: 50px;
}

.Tab-logo{
  margin: 5px;
}

.Tab-text{
  margin: 5px;
}

.Tab-button{
  margin: 5px;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
